import MasterdataRepository from './b8one.masterdataRepository';

/**
 * MoveListSaveRepository class
 */
export default class MoveListSaveRepository extends MasterdataRepository {
  constructor() {
    super('LS');
  }

  /**
   * This function add a new document to LS entity on Master Data
   * @param  {String} name
   * @param  {String} email
   * @param  {String} date
   * @param  {Array|String} items
   */
  async addList(name, email, date, items) {
    const apiEndpoint = `${this._VTEX_MASTERDATA_ENDPOINT}/documents`;

    if (Array.isArray(items)) {
      items = JSON.stringify(items);
    }

    return this.httpClient_.post(apiEndpoint, {
      listName: name,
      listDate: date,
      listEmail: email,
      listItems: items,
    });
  }

  async getListByQuery(
    query,
    fields = [
      'id',
      'listName',
      'listEmail',
      'homeType',
      'homeSize',
      'listDate',
      'listItems',
      'isTemplate',
    ]
  ) {
    const params = new URLSearchParams();

    params.append('_where', query);
    params.append('_fields', `${fields}`);

    console.log(params);

    return this.search(params);
  }

  /**
   * This function get the documents of LS entity on Master Data by email field
   * @param  {String} email
   * @param  {Array|String} fields=['id', 'listName', 'listEmail', 'listDate', 'listItems', 'isTemplate']
   */
  async getAllListsByEmail(
    email,
    fields = [
      'id',
      'listName',
      'listEmail',
      'listDate',
      'homeType',
      'homeSize',
      'listItems',
      'isTemplate',
    ]
  ) {
    const params = new URLSearchParams();

    params.append('_where', `(listEmail=${email} AND isTemplate=false)`);
    params.append('_fields', `${fields}`);

    return this.search(params);
  }

  /**
   * This function get the lists templates of LS entity on Master Data
   * @param  {Array|String} ['id', 'listName', 'listRooms', 'listExcerpt', 'listDescription']
   */
  async getAllListsTemplates(
    fields = ['id', 'listName', 'listRooms', 'listExcerpt', 'listDescription']
  ) {
    const params = new URLSearchParams();

    params.append('_where', `isTemplate=true`);
    params.append('_fields', `${fields}`);

    return this.search(params);
  }

  /**
   * This function get the documents of LS entity on Master Data by DocumentId
   * @param  {String} DocumentId
   * @param  {Array|String} fields=['id', 'listName', 'listDate', 'listItems', 'listRooms', 'listExcerpt', 'listDescription', 'isTemplate']
   */
  async getListByDocumentId(
    DocumentId,
    fields = [
      'id',
      'listName',
      'listDate',
      'listItems',
      'listRooms',
      'homeType',
      'homeSize',
      'listExcerpt',
      'listDescription',
      'isTemplate',
    ]
  ) {
    const params = new URLSearchParams();

    params.append('_fields', `${fields}`);

    return this.getByDocumentIdWithFields(DocumentId, fields);
  }

  /**
   * @param  {String} DocumentId
   */
  async deleteListByDocumentId(DocumentId) {
    return this.delete(DocumentId);
  }
}
