import axios from 'axios';

export default class GoogleMapsService {
  constructor() {
    this.API_KEY = 'AIzaSyAyaW4zHxd3U7PLEUQtbeeEPOLHs6VkP8g';

    this.api = axios.create({
      baseURL: 'https://maps.googleapis.com/maps/api',
    });
  }

  /**
   * Verify if zipcode is between valid range
   * @param  {String} zipcode The zipcode to verify
   * @param  {String} initialRange The initial range used to valite the zipcode
   * @param  {String} finalRange The final range used to valite the zipcode
   */
  isZipcodeValidRange(zipcode, initialRange, finalRange) {
    zipcode = this.convertZipcodeToFloat(zipcode);
    initialRange = this.convertZipcodeToFloat(initialRange);
    finalRange = this.convertZipcodeToFloat(finalRange);

    return zipcode >= initialRange && zipcode <= finalRange;
  }

  /**
   * Convert brazilian's zipcode to decimal
   * @param {String} zipcode zipcode string format (ex.: 12345-678)
   */
  convertZipcodeToFloat(zipcode) {
    const textRemoved = zipcode.replace('-', '');
    const addDecimalDot = `${textRemoved.slice(0, 1)}.${textRemoved.slice(1)}`;

    return parseFloat(addDecimalDot).toFixed(7);
  }

  async getPlaceWithLatLng(lat, lng) {
    try {
      return await this.api.get('/geocode/json', {
        params: {
          latlng: `${lat},${lng}`,
          key: this.API_KEY,
        },
      });
    } catch (error) {
      console.error(
        'Error in GoogleMapsService on method getPlaceWithLatLng',
        error
      );
    }

    return;
  }
}
