import HttpClient from './b8one.httpClient';

/**
 * VtexUser Abstraction class
 */
class VtexUser {
  /**
   * VtexUser constructor
   */
  constructor() {
    this._httpClient = new HttpClient();
    this._vtexCurrentUserEndpoint = '/api/vtexid/pub/authenticated/user';
  }

  /**
   * Get current user information
   * @return {Object} VTEX User Object
   */
  getCurrent() {
    return this._httpClient.get(this._vtexCurrentUserEndpoint);
  }

  /**
   * Open authenticate modal
   * @param {*} language
   * @param {*} forceReload
   */
  authenticateModal(
    options = { language: 'pt-br', forceReload: true, forceScrollTop: false }
  ) {
    window.vtexid.start({
      locale: options.language,
      forceReload: options.forceReload,
    });

    if (options.forceScrollTop) {
      window.scrollTo(0, 0);
    }
  }
}

export default VtexUser;
